import React from 'react';
import './estilo.css'

import EstruturaPagina from '../../componentes/EstruturaPagina';


const Missao = () =>{
  return(
        <EstruturaPagina>
        <div className="home-apresentacao">
          <h1>Nossa clínica, valores e missão</h1>
        </div>
        <div className="missao-apresentacao">
          <p className="missao">Desde 2008, quando inauguramos a clínica, buscamos oferecer procedimentos de qualidade e as melhores soluções em odontologia preventiva aos nossos pacientes. Nossa missão é satisfazer as pessoas que atendemos através de uma metodologia que proporcione a saúde bucal. Isso só é possível através de valores que envolvam profissionalismo, seriedade, competência e respeito ao próximo. Colocando esses recursos em prática e procurando sempre proporcionar o melhor atendimento, nossa visão é ser uma empresa referência na prestação de serviços odontológicos e promover a saúde bucal dos nossos pacientes. Afinal, se “o sorriso é a menor distância entre duas pessoas, temos inúmeros motivos para trabalhar com qualidade, alegria, entusiasmo e orgulho da profissão que abraçamos.”</p> <br />
          <ul className="missao-destaques">
            <li>Soluções em odontologia preventiva</li>
            <li>Busca pela satisfação dos pacientes</li>
            <li>Profissionais capacitados</li>
            <li>Atendimento individual e personalizado</li>
            <li>Seriedade, competência e respeito ao próximo</li>
          </ul>
        </div>
         
    
        </EstruturaPagina>
  );
}
export default Missao;